body {
  --lt-grey: #d6d6d6;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  min-height: 100vh;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#0b1414), to(#0b141499));
  background-image: -webkit-linear-gradient(top, #0b1414, #0b141499);
  background-image: -o-linear-gradient(top, #0b1414, #0b141499);
  background-image: linear-gradient(to bottom, #0b1414, #0b141499);
  background-color: #0b1414;
  background-position: 0 680px;
}


